import Citys from './citys'
export default {
  props: {
    hotCitys: {
      type: Array,
      default () {
        return []
      }
    },
    value: {
      type: String,
      default: '未设置'
    }
  },
  data() {
    return {
      citys: Citys,
      windowHeight: '',
      scrollIntoId: 'F',
      current: this.value
    }
  },
  mounted() {
    this.getSystemInfo()
  },
  methods: {
    getSystemInfo() {
      uni.getSystemInfo().then(res => {
        let [error, data] = res
        this.windowHeight = `${data.windowHeight}px`
      })
    },
    scrollTo(letter) {
      this.scrollIntoId = letter === '#' ? 'current' : letter
    },
    onSelect(city) {
      this.current = city
      this.$emit('input', city)
      this.$emit('on-select', city)
    }
  }
}
